import { FileText, Mail, Github } from "lucide-react";
import { Button } from "@/components/ui/button";
import { portfolioData } from "@/data/portfolio";

export function HeroSection() {
  const { personal, contact } = portfolioData;

  return (
    <header className="flex flex-col items-start px-4 pt-16 pb-8 md:px-8 lg:px-16">
      <div className="flex flex-col gap-3 mb-8">
        <h1 className="text-4xl font-bold text-foreground md:text-4xl lg:text-5xl">
          {personal.name}
        </h1>
        <p className="text-lg text-foreground md:text-xl">
          {personal.title}
        </p>
        <p className="text-base text-foreground md:text-lg">
          {personal.location}
        </p>
      </div>
      <div className="flex flex-wrap gap-3">
        <Button asChild>
          <a href={contact.resume} target="_blank" rel="noopener noreferrer">
            <FileText className="h-4 w-4" />
            Resume
          </a>
        </Button>
        <Button asChild>
          <a href={contact.email}>
            <Mail className="h-4 w-4" />
            Email
          </a>
        </Button>
        <Button asChild>
          <a
            href={contact.github}
            target="_blank"
            rel="noopener noreferrer"
          >
            <Github className="h-4 w-4" />
            GitHub
          </a>
        </Button>
      </div>
    </header>
  );
}

