import { Github, ExternalLink, Palette, Code, Layers, Zap, Rocket, Globe } from "lucide-react";
import { portfolioData } from "@/data/portfolio";
import { Card, CardContent, CardFooter, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { ComponentType } from "react";

// Map technology names to Lucide icons
const techIconMap: Record<string, ComponentType<{ className?: string }>> = {
  "CSS3": Palette,
  "JavaScript": Code,
  "Material-UI": Layers,
  "React": Zap,
  "Vite": Rocket,
  "i18n": Globe,
  "i18next": Globe,
};

export function ProjectsSection() {
  return (
    <section className="px-4 py-16 md:px-8 lg:px-16">
      <h2 className="mb-8 text-center text-3xl font-bold text-foreground md:text-4xl">
        Projects
      </h2>
      <div className="grid gap-6 md:grid-cols-2">
        {portfolioData.projects.map((project, index) => (
          <Card key={index} className="bg-white shadow-sm">
            <CardHeader>
              <CardTitle className="text-xl font-semibold text-foreground md:text-xl">
                {project.title}
              </CardTitle>
            </CardHeader>
            <CardContent>
              <p className="mb-4 text-base leading-relaxed" style={{ color: '#737373' }}>
                {project.description}
              </p>
              <div className="flex flex-wrap gap-2">
                {project.technologies.map((tech, techIndex) => {
                  let variant: "default" | "secondary" | "purple" = "default";
                  if (tech.color === "yellow") {
                    variant = "secondary";
                  } else if (tech.color === "purple") {
                    variant = "purple";
                  }
                  const IconComponent = techIconMap[tech.name] || Code;
                  return (
                    <Badge key={techIndex} variant={variant}>
                      <IconComponent className="h-3 w-3" />
                    </Badge>
                  );
                })}
              </div>
            </CardContent>
            <CardFooter className="flex gap-3">
              {project.links.map((link, linkIndex) => (
                <Button key={linkIndex} asChild >
                  <a
                    href={link.url}
                    target={link.type === "demo" ? "_blank" : undefined}
                    
                    rel={
                      link.type === "demo"
                        ? "noopener noreferrer"
                        : undefined
                    }
                  >
                    {link.type === "github" ? (
                      <Github className="h-4 w-4" />
                    ) : (
                      <ExternalLink className="h-4 w-4" />
                    )}
                    {link.label}
                  </a>
                </Button>
              ))}
            </CardFooter>
          </Card>
        ))}
      </div>
    </section>
  );
}

